
.service-card {
  position: relative;
  transition: box-shadow 0.3s;
  width: 100%;
  min-height: 100px;
}
.service-card:hover {
  box-shadow: 0 8px 10px 1px rgba(0,0,0,0.07), 0 3px 14px 2px rgba(0,0,0,0.1), 0 5px 5px -3px rgba(0,0,0,0.06);
}
.service-card:hover .service-card__toolbar {
  opacity: 1;
}
.service-card:hover .service-card__title {
  color: #0070c5;
}
.service-card__link {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 2;
  border-bottom: 0;
}
.service-card__meta {
  color: #888;
  font-size: 13px;
  margin-top: 16px;
  margin-bottom: -6px;
}
.service-card__meta .service-card__meta-item {
  position: relative;
  margin-left: 24px;
}
.service-card__meta .service-card__meta-item::before {
  content: "";
  position: absolute;
  left: -16px;
  top: 6px;
  width: 5px;
  height: 5px;
  background-color: #c9dced;
  border-radius: 50%;
}
.service-card__meta .service-card__meta-item:first-child {
  margin-left: 0;
}
.service-card__meta .service-card__meta-item:first-child::before {
  display: none;
}
.service-card__meta .service-card__type {
  text-transform: uppercase;
}
.service-card__title {
  font-size: 19px;
  padding-left: 34px;
  padding-right: 80px;
  margin-bottom: 8px;
  font-weight: bold;
}
.service-card__title .service-card__icon {
  float: left;
  margin-left: -34px;
  margin-top: 1px;
}
.service-card__toolbar {
  position: absolute;
  right: 4px;
  bottom: 4px;
  z-index: 10;
  opacity: 0;
}
.service-card__status {
  position: absolute;
  top: 14px;
  right: 14px;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background-color: #0070c5;
  z-index: 10;
}
.icon-toolbar--v .icon-toolbar__btn {
  display: flex;
  margin: 0;
}
.icon-toolbar--s .icon-toolbar__btn {
  width: 32px;
  height: 32px;
}
.icon-toolbar--s .icon-toolbar__btn .icon {
  font-size: 22px;
}


