  line-height: inherit;
}
.btn.btn--outline {
  border: 1px solid currentColor;
  background: transparent !important;
  box-shadow: none;
}
.btn.btn--outline:hover {
  box-shadow: none;
}
.btn--block {
  display: flex;
  flex: 1;
  margin: 6px 0;
  width: 100%;
}
.btn--round {
  border-radius: 28px;
}
.btn--round:after {
  border-radius: 28px;
}
.btn .icon--right {
  margin-left: 16px;
}
.btn .icon--left {
  margin-right: 16px;
}
.btn.primary,
.btn.secondary,
.btn.accent,
.btn.success,
.btn.error,
.btn.warning,
.btn.info {
  color: #fff;
}
.theme--light .icon,
.application .theme--light.icon {
  color: rgba(0,0,0,0.54);
}
.theme--light .icon.icon--disabled:not(.input-group__append-icon),
.application .theme--light.icon.icon--disabled:not(.input-group__append-icon) {
  color: rgba(0,0,0,0.38) !important;
}
.theme--dark .icon,
.application .theme--dark.icon {
  color: #fff;
}
.theme--dark .icon.icon--disabled:not(.input-group__append-icon),
.application .theme--dark.icon.icon--disabled:not(.input-group__append-icon) {
  color: rgba(255,255,255,0.5) !important;
}
.icon {
  align-items: center;
  display: inline-flex;
  font-feature-settings: 'liga';
  font-size: 24px;
  justify-content: center;
  line-height: 1;
  transition: 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
  vertical-align: middle;
}
.icon.icon--large {
  font-size: 2.5rem;
}
.icon.icon--medium {
  font-size: 2rem;
}
.icon.icon--x-large {
  font-size: 3rem;
}
.icon.icon--disabled {
  pointer-events: none;
}
.dialog {
  box-shadow: 0px 11px 15px -7px rgba(0,0,0,0.2), 0px 24px 38px 3px rgba(0,0,0,0.14), 0px 9px 46px 8px rgba(0,0,0,0.12);
  border-radius: 2px;
  margin: 24px;
  overflow-y: auto;
  pointer-events: auto;
  transition: 0.3s ease-in-out;
  width: 100%;
  z-index: inherit;
}
.dialog__content {
  align-items: center;
  display: flex;
  height: 100%;
  justify-content: center;
  left: 0;
  pointer-events: none;
  position: fixed;
  top: 0;
  transition: 0.3s ease-in-out;
  width: 100%;
  z-index: 6;
  outline: none;
}
.dialog:not(.dialog--fullscreen) {
  max-height: 90%;
}
.dialog__container {
  display: inline-block;
  vertical-align: middle;
}
.dialog--fullscreen {
  margin: 0;
  height: 100%;
  position: fixed;
  overflow-y: auto;
  top: 0;
  left: 0;
}
.dialog--fullscreen > .card {
  min-height: 100%;
  min-width: 100%;
  margin: 0 !important;
  padding: 0 !important;
}
.dialog--scrollable {
  display: flex;
}
.dialog--scrollable > .card {
  display: flex;
  flex: 1 1 auto;
  flex-direction: column;
}
.dialog--scrollable > .card > .card__title,
.dialog--scrollable > .card > .card__actions {
  flex: 1 0 auto;
}
.dialog--scrollable > .card > .card__text {
  overflow-y: auto;
  backface-visibility: hidden;
}
.theme--light .card,
.application .theme--light.card {
  background-color: #fff;
  color: rgba(0,0,0,0.87);
}
.theme--dark .card,
.application .theme--dark.card {
  background-color: #424242;
  color: #fff;
}
.card {
  display: block;
  border-radius: 2px;
  min-width: 0;
  position: relative;
  text-decoration: none;
  box-shadow: 0px 2px 1px -1px rgba(0,0,0,0.2), 0px 1px 1px 0px rgba(0,0,0,0.14), 0px 1px 3px 0px rgba(0,0,0,0.12);
}
.card > *:first-child:not(.btn) {
  border-top-left-radius: inherit;
  border-top-right-radius: inherit;
}
.card > *:last-child:not(.btn) {
  border-bottom-left-radius: inherit;
  border-bottom-right-radius: inherit;
}
.card--raised {
  box-shadow: 0px 3px 3px -2px rgba(0,0,0,0.2), 0px 3px 4px 0px rgba(0,0,0,0.14), 0px 1px 8px 0px rgba(0,0,0,0.12);
}
.card--tile {
  border-radius: 0;
}
.card--flat {
  box-shadow: 0px 0px 0px 0px rgba(0,0,0,0.2), 0px 0px 0px 0px rgba(0,0,0,0.14), 0px 0px 0px 0px rgba(0,0,0,0.12);
}
.card--hover {
  cursor: pointer;
  transition: all 0.4s cubic-bezier(0.25, 0.8, 0.25, 1);
  transition-property: box-shadow;
}
.card--hover:hover {
  box-shadow: 0px 5px 5px -3px rgba(0,0,0,0.2), 0px 8px 10px 1px rgba(0,0,0,0.14), 0px 3px 14px 2px rgba(0,0,0,0.12);
}
.card__title {
  align-items: center;
  display: flex;
  flex-wrap: wrap;
  padding: 16px;
}
.card__title--primary {
  padding-top: 24px;
}
.card__text {
  padding: 16px;
  width: 100%;
}
.card__media {
  display: flex;
  overflow: hidden;
  position: relative;
}
.card__media img {
  width: 100%;
}
.card__media__background {
  border-radius: inherit;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}
.card__media__content {
  display: flex;
  flex: 1 1 auto;
  position: relative;
}
.card__actions {
  align-items: center;
  display: flex;
  padding: 8px 4px;
}
.card__actions > *,
.card__actions .btn {
  margin: 0 4px;
}
.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  pointer-events: none;
  transition: 0.5s cubic-bezier(0.25, 0.8, 0.25, 1);
  z-index: 5;
}
.overlay--absolute {
  position: absolute;
}
.overlay:before {
  background-color: #212121;
  bottom: 0;
  content: '';
  height: 100%;
  left: 0;
  opacity: 0;
  position: absolute;
  right: 0;
  top: 0;
  transition: inherit;
  transition-delay: 150ms;
  width: 100%;
}
.overlay--active {
  pointer-events: auto;
  touch-action: none;
}
.overlay--active:before {
  opacity: 0.46;
}
h1,
.display-4 {
  font-family: "Ubuntu";
}
h2,
.display-3 {
  font-family: "Ubuntu";
}
h3,
.display-2 {
  font-family: "Ubuntu";
}
h4,
.display-1 {
  font-family: "Ubuntu";
}
h5,
.headline {
  font-family: "Ubuntu";
}
h6,
.title {
  font-family: "Ubuntu";
}
.subheading {
  font-family: "Ubuntu";
}
.pagination {
  margin-top: 24px;
}
.pagination__item {
  border: 0;
  box-shadow: none;
}
.pagination__item:hover,
.pagination__item:active,
.pagination__item:focus {
  color: rgba(0,0,0,0.87);
}
.pagination__item--active {
  box-shadow: 0px 2px 4px -1px rgba(0,0,0,0.2), 0px 4px 5px 0px rgba(0,0,0,0.14), 0px 1px 10px 0px rgba(0,0,0,0.12);
}
.pagination__item--active:hover,
.pagination__item--active:active,
.pagination__item--active:focus {
  color: #fff;
  box-shadow: 0px 2px 4px -1px rgba(0,0,0,0.2), 0px 4px 5px 0px rgba(0,0,0,0.14), 0px 1px 10px 0px rgba(0,0,0,0.12);
}
.pagination__navigation {
  box-shadow: none;
  border: 0;
  margin-left: 0;
  margin-right: 0;
  padding: 0;
}
.btn {
  text-transform: none;
  height: auto;
  font-weight: 500;
  letter-spacing: 0;
}
.btn--dark:hover:after {
  background-color: transparent;
}
.btn--small {
  padding: 8px 16px;
  font-weight: 400;
}
a:hover .icon,
a:hover .material-icons {
  color: #0070c5;
}
.card__actions {
  padding: 8px 12px 16px;
}
.theme--light .heading,
.application .theme--light.heading {
  color: rgba(0,0,0,0.87);
}
.theme--dark .heading,
.application .theme--dark.heading {
  color: #fff;
}


